import React,{ Component } from 'react';
import { LivePreview } from './LivePreview';
import { SlideShowButton } from './SlideShowButton';
import { MarkdownButton } from './MarkdownButton';

export class SlideShowApp extends Component {
	constructor(props,context) {
		super(props,context);
		this.state = {
			text:''
		};
		this.handlechange = this.handlechange.bind(this);
	}

	componentWillUnmount() {
	}

	componentDidMount() {
	}

	handlechange(e) {
		this.setState({
			text: e.target.value,
			canPlay: e.target.value !== ''
		});
	}
	//將markdown的文字訊息利用props傳給Slideshowbuttn-->
	render() {
		return (
			React.createElement("div", {className: "row"}, 
				React.createElement("div", {className: "col-md-6"}, 
					React.createElement("h1", null, "You can write something at here"), 
					React.createElement(MarkdownButton, null), 
					React.createElement("textarea", {
						className: "form-control", 
						onChange: this.handlechange, 
						rows: "30"}
					)
				), 
				React.createElement("div", {className: "col-md-6"}, 
					React.createElement("h1", null, "Here will show something you write"), 
					React.createElement("span", {"data-toggle": "buttons"}, 
						React.createElement(SlideShowButton, {
							className: "btn btn-default btn-circle btn-lg", 
							canPlay: this.state.canPlay, 
							text: this.state.text}
						 )
					), 
					React.createElement(LivePreview, {
						text: this.state.text}
					)
				)
			)
		);
	}
}

SlideShowApp.defaultProps = {
	
}